@import "../../style/variables.scss";

.search {
  margin-right: 5%;
  margin-top: 13px;
  position: relative;
  .searchInput {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  input[type="text"] {
    border: 1px solid #ddd;
    color: #555;
    font-size: 14px;
    font-family: "Raleway", sans-serif;
    height: 40px;
    padding: 10px 55px 10px 10px;
    width: 500px;
    outline: none;
  }
  button {
    height: 40px;
    width: 50px;
    transition: all 0.5s;
    background-color: #ff6666;
    outline: none;
    border: none;
    cursor: pointer;
    &:hover {
      background-color: #ff9494;
    }
    .MuiSvgIcon-root {
      color: white;
    }
  }
  .searchResults {
    position: absolute;
    z-index: 1111;
    background-color: white;
    box-shadow: 0px 0px 8px rgba(16, 30, 115, 0.12);
    .dataResults {
      padding: 15px;
      transition: 0.3s;
      &:hover {
        background-color: $mainColor;
      }
      .dataResultLink {
        display: flex;
        .dataResultImage {
          width: 100px;
          height: 100px;
          img {
            width: 100%;
            height: 100%;
            object-fit: contain;
          }
        }
        .dataResultText {
          margin-left: 10px;
          width: 100%;
          a {
            color: black;
            transition: 0.3s;
            &:hover {
              color: $mainColor;
            }
          }
        }
      }
    }
  }
}
